<template>
  <div>
    <div class="breadcrumb">
      <el-row justify="center" type="flex">
        <el-col :span="1">
        </el-col>
        <el-col :span="22" style="width:1438px">
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }">Home</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: '/contact' }">Contact</el-breadcrumb-item>
          </el-breadcrumb>
        </el-col>
        <el-col :span="1">
        </el-col>
      </el-row>
    </div>

    <el-row justify="center" type="flex" style="background:#E6A23C;height: 80px;">
      <el-col :span="1">
      </el-col>
      <el-col :span="22" style="width:1438px">
        <h1 style="color:#F2F6FC;text-align: left;">Contact Us</h1>
      </el-col>
      <el-col :span="1">
      </el-col>
    </el-row>

    <el-row justify="center" type="flex" style="height:50px">
      <el-col :span="1">
      </el-col>
      <el-col :span="22" style="width:1438px">
      </el-col>
      <el-col :span="1">
      </el-col>
    </el-row>

    <el-row class="cardinput" justify="center" type="flex" style="height: auto;">
      <el-col :span="1">
      </el-col>
      <el-col :span="22" style="width:1438px">
        <el-card>
          <el-form v-loading="loadingform" :model="ruleForm" status-icon ref="ruleForm" label-width="100px"
            label-position="left">
            <el-form-item label="Name" prop="user_name">
              <el-input type="text" v-model="ruleForm.user_name" placeholder="* Name"></el-input>
            </el-form-item>
            <el-form-item label="Email" prop="user_email">
              <el-input type="email" v-model="ruleForm.user_email"
                placeholder="* Email (We will reply you via email in 24 hours)"></el-input>
            </el-form-item>
            <el-form-item label="Phone" prop="user_phone">
              <el-input type="tel" v-model="ruleForm.user_phone"
                placeholder="Phone/WhatsApp/WeChat (Very important)"></el-input>
            </el-form-item>
            <el-form-item label="Message" prop="message">
              <el-input type="textarea" v-model="ruleForm.message"
                placeholder="* Enter product details such as size, color, materials etc. and other specific requirements to receive an accurate quote."></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="warning" @click="submitForm('ruleForm')">Send</el-button>
              <el-button @click="resetForm('ruleForm')">Empty</el-button>
            </el-form-item>
          </el-form>
        </el-card>
      </el-col>
      <el-col :span="1">
      </el-col>
    </el-row>

    <el-row justify="center" type="flex">
      <el-col :span="1">
      </el-col>
      <el-col :span="22" style="width:1438px">
        <el-divider></el-divider>
      </el-col>
      <el-col :span="1">
      </el-col>
    </el-row>


    <el-row justify="center" type="flex" style="height: auto;">
      <el-col :span="1">
      </el-col>
      <el-col :span="22" style="width:1438px">
        <el-card>
          <el-descriptions direction="vertical" :column="4" border>
            <el-descriptions-item label="name">Sherry</el-descriptions-item>
            <el-descriptions-item label="phone">+86-15168197984</el-descriptions-item>
            <el-descriptions-item label="mail" :span="2">sherry@dvtspring.com</el-descriptions-item>
            <el-descriptions-item label="name">Judy</el-descriptions-item>
            <el-descriptions-item label="phone">+86-13918353649</el-descriptions-item>
            <el-descriptions-item label="mail" :span="2">zhuting@dvtspring.com</el-descriptions-item>
            <el-descriptions-item label="note">
              <el-tag size="small">null</el-tag>
            </el-descriptions-item>
            <el-descriptions-item label="Address">#11-3, Huiding Chuangzhi Park, 277#Shengyuan Road, 315500 Fenghua,
              Ningbo City, Zhejiang Pro., China</el-descriptions-item>
          </el-descriptions>
        </el-card>
      </el-col>
      <el-col :span="1">
      </el-col>
    </el-row>

    <el-row justify="center" type="flex">
      <el-col :span="1">
      </el-col>
      <el-col :span="22" style="width:1438px">
        <el-divider></el-divider>
      </el-col>
      <el-col :span="1">
      </el-col>
    </el-row>

  </div>
</template>

<script>
//smtp.exmail.qq.com(使用SSL，端口号465)
//jordan@eastonspring.com
//T4wYzdLkBMMw7Jys
import emailjs from '@emailjs/browser';

export default {
  data() {
    return {
      ruleForm: {
        user_name: '',
        user_email: '',
        user_phone: '',
        message: ''
      },
      loadingform: false
    }
  },
  methods: {
    submitForm() {
      this.loadingform = true
      emailjs.send('service_mvx8z1x', 'template_sskegur', this.ruleForm, 'stcFLZDlPv70r9BPd')
        .then(function (response) {
          console.log('SUCCESS!', response.status, response.text);
        }, function (error) {
          console.log('FAILED...', error);
        });
      setTimeout(() => {
        this.loadingform = false
      }, 2000)
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    openFullScreen() {
      const loading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      return loading;
    },
    closeFullScreen(loading) {
      loading.close();
    },
  }
}

</script>

<style scoped>
#app {
  font-family: Helvetica, sans-serif;
  margin: -8px -8px auto;
}

.cardinput>>>.el-input__inner:focus {
  border-color: #E6A23C !important;
}

.cardinput>>>.el-textarea__inner {
  border-color: #E6A23C !important;
}

.breadcrumb {
  background-color: #74706a;
  height: 30px;
}

.breadcrumb>>>.el-breadcrumb {
  margin-top: 0.5%;
}

.breadcrumb>>>.el-breadcrumb__inner {
  color: aliceblue;
  font-size: 16px;
}

.breadcrumb>>>.el-breadcrumb__separator {
  color: #E6A23C;
}

.breadcrumb>>>.el-breadcrumb__inner:hover {
  color: #E6A23C;
}
</style>



